import React, { Component } from 'react';
import Section from '../components/Section';
import '../styles/sections.css';

class Sections extends Component {
	constructor() {
		super();
		this.state = {
			sections: <div className="message paper shadow center">&middot;&middot;&middot; Please wait &middot;&middot;&middot;</div>,
			data: []
		}
	}

	componentDidMount(){
		fetch("https://gist.githubusercontent.com/hifebriansyah/5f62028cf655a6b5af592986057d88ef/raw/sections-"+this.props.match.params.section.toLowerCase()+".json")
			.then(response => {
				if (!response.ok) { throw response }
				return response.json();
			})
			.then(result => {
				let data = result.data;

				let sections = data.map((section, i) => {
					return (
						<Section 
							lang={this.props.lang}
							title={section.title}
							key={i}
							type={section.type}
							chapters={section.chapters}
							section={this.props.match.params.section} />
					)                 
				});

				this.setState({sections, data}, () => {
					this.onTypeToggle(null, 1);
					document.querySelector(".type-toggle").classList.add('block');
					this.props.setCutter('medium');
				})
			})
			.catch( err => {
				err.text().then( errorMessage => {
					console.warn(errorMessage);
					document.querySelector(".message ").innerHTML = '&middot;&middot;&middot; UNDER DEVELOPMENT &middot;&middot;&middot;';
				})
			});
	}

	onTypeToggle(e, active_type) {
		document.querySelectorAll(".section").forEach(element => {
			element.classList.remove("block")
			element.classList.add("none")
		});

		document.querySelectorAll(".section.type-" + active_type).forEach(element => {
			element.classList.remove("none")
			element.classList.add("block")
		});

		let sectionBlocks = document.querySelectorAll(".section.block");

		sectionBlocks[0].classList.add("first");
		sectionBlocks[sectionBlocks.length-1].classList.add("last");

		if(e) {
			document.querySelectorAll(".type-toggle > div").forEach(element => {
				element.classList.remove("active")
			});

			e.target.classList.add('active')
		};
	}

	render() {
	    return(
	        <div className="sections page">
	        	<div className="type-toggle clear">
	        		<div onClick={(e, active_type) => this.onTypeToggle(e, 1)} className="active">Exams</div>
	        		<div onClick={(e, active_type) => this.onTypeToggle(e, 2)}>Lessons</div>
	        	</div>
	        	<div>
	    			{this.state.sections}
	    		</div>
	        </div>
	    )
	}
}

export default Sections;