import React from 'react';
import { Link } from "react-router-dom";
import '../styles/section.css';

const Section = (props) => {
	let chapters = props.chapters.map((chapter, i) => {     
		return (
			<Link key={i} to={"/courses/"+props.section+"/"+chapter.link[props.lang]} className="chapter clear">
				<div className="name">{chapter.title[props.lang]}</div>
				<div className="arrow"></div>
			</Link>
		)                 
	})

    return(
        <div className={"type-" + props.type.id + " section"}>
        	<div className="top shadow" style={{backgroundImage:'linear-gradient(rgba(100, 100, 100, 0), rgba(100, 100, 100, 0.8)), url(/'+ props.section.toLowerCase() +'.jpeg)'}}>
				<div>
					<h1>{props.title[props.lang]}</h1>
					<p>{props.type[props.lang]}</p>
				</div>
			</div>
    		<div className="chapters shadow">
    			{chapters}
    		</div>
    	</div>
    )
}

export default Section;