import React from "react";
import '../styles/course.css';
import { Link } from "react-router-dom";

const Course = (props) => {
	let btnClass = (1) ? 'blue' : 'grey';
	let btnLabel = (1) ? "Let's go!" : 'Coming Later!';

	let item = props.items.map((item, i) => {
		let priceClass = (item.status < 3) ? 'green' : 'red';
		
		return (
			<tr key={i}>
				<td>{item.total}</td>
				<td>{item.title[props.lang]}</td>
				<td className={priceClass}>{item.price[props.lang]}</td>
			</tr>
		)                 
	});

    return(
		<div className="course shadow">
			<div className="top shadow" style={{backgroundImage:'linear-gradient(rgba(100, 100, 100, 0), rgba(100, 100, 100, 0.8)), url(/'+props.link['en'].toLowerCase()+'.jpeg)'}}>
				<div>
					<h1>{props.title[props.lang]}</h1>
					<p>{props.content[props.lang]}</p>
				</div>
			</div>
			<div className="bottom">
				<span>This section includes</span>  :
				<table>
					<tbody>
						{item}
					</tbody>
				</table>
				<Link to={"/courses/"+props.link[props.lang]} className={btnClass +" btn shadow"}>{btnLabel}</Link>
			</div>
		</div>
    )
}

export default Course;
